import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Ayats, HeaderSurat } from "../../componenets";
import ReactLoading from "react-loading";

const Surat = () => {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(true);

  const [items, setItems] = useState([]);
  const [header, SetHeader] = useState([]);
  const { state } = useLocation();
  const data = state;
  useEffect(() => {
    fetch(`https://api.quran.sutanlab.id/surah/${data}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setIsLoaded(false);
          // const parse = Object.keys(result.data);
          setItems(result.data.verses);
          // console.log(parse);
          // console.log(result.data.verses);
          // console.log(result.data.name.long);
          SetHeader(result.data.name.long);
          console.log(result.data.verses.length);
        },
        (error) => {
          setIsLoaded(false);
          setError(error);
        }
      );
  }, []);
  return (
    <>
      {isLoaded && (
        <ReactLoading
          type={"Bars"}
          color={"blue"}
          height={"100%"}
          width={"100%"}
        />
      )}
      <div class="h-100  bg-white   lg:py-16 justify-center  items-center	">
        <div class="gap-5 	">
          <HeaderSurat title={`${header}`} />
          <div class="py-5" />

          {items.map((item) => (
            <Ayats
              url={item.audio.primary}
              namaAyat={item.text.arab}
              arti={item.translation.id}
              no={item.number.inSurah}
              onWheel={() => console.log(item.number.inSurah)}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default Surat;
