import { Link } from "react-router-dom";
import Player from "../Player";
import "./ayats.css";

const Ayats = ({ url, namaAyat, arabic, arti, no, onWheel }) => {
  return (
    <>
      <div class="container  bg-white  flex flex-col mb-4 ">
        <div class="font-text-bold justify-between  text-sm text-gray-400 flex flex-row bg-gray-100 mx-2  p-1 rounded">
          <div
            class="text-black text-sm rounded w-10 flex items-center justify-center h-5"
            style={{ backgroundColor: "#96D955" }}
          >
            {no}
          </div>
          <div class="rounded bg-gray-800 px-2 flex items-center justify-center h-5">
            <Player url={url} />
          </div>
        </div>
        <button onWheel={onWheel}>
          <div class=" hover:bg-gray-100  bg-white p-2 border-b border-fuchsia-800">
            <div class="  p-1 flex flex-col">
              <div class=" p-1 text-4xl font-arabic text-gray-900 mb-3  flex justify-end	">
                {namaAyat}
              </div>
              <div class="mb-1 p-1 text-xs  text-gray-900 font-text text-base flex justify-start	">
                {arti}
              </div>
            </div>
          </div>
        </button>
      </div>
    </>
  );
};

export default Ayats;
