import React, { useEffect, useState } from "react";
import { Ayat } from "../../componenets";
import { Link, useHistory } from "react-router-dom";

const Home = () => {
  const history = useHistory();
  const handleClick = () => {
    history.push("/Surat");
  };

  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);

  useEffect(() => {
    fetch("https://api.quran.sutanlab.id/surah")
      .then((res) => res.json())
      .then(
        (result) => {
          setIsLoaded(true);

          setItems(result.data);

          console.log(result.data);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
  }, []);

  return (
    <>
      <div className="d-flex fixed w-100 justify-content-center p-3 bg-green-900 d-block d-sm-none d-none d-sm-block d-md-none">
        <div className="text-white"> Alquran App</div>
      </div>
      <div class="bg-gray-50 h-100 pt-20 pb-0">
        <div class="container 	  ">
          <div class="gap-5  grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4">
            {items.map((item) => (
              <Link
                to={{
                  pathname: "/Surat",
                  state: item.number
                }}
              >
                <Ayat
                  // onClick={handleClick}
                  key={item.id}
                  no={item.number}
                  namaAyat={item.name.transliteration.id}
                  arabic={item.name.short}
                  arti={item.name.translation.id}
                />
              </Link>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
