import "./header.css";

const HeaderSurat = ({ title, desc }) => {
  return (
    <>
      <div class="py-2 justify-center flex border-2 rounded border-gray-500	  bg-green-600 mb-3 fixed w-100">
        <div>
          <div class="font-arabic text-4xl text-gray-200">{title}</div>
          <div>{desc}</div>
        </div>
      </div>
    </>
  );
};

export default HeaderSurat;
