import { Link } from "react-router-dom";
import "./ayat.css";

const Ayat = ({ namaAyat, arabic, arti, no, onClick }) => {
  return (
    <>
      <div
        onClick={onClick}
        class="rounded  effect-sahdow border border-indigo-800  bg-green-900 p-2 flex flex-row "
      >
        <div class="w-1/5  p-1 flex items-center text-white">{no}</div>
        <div class="w-3/5  p-1 flex flex-col">
          <div class=" p-1 text-sm text-white">{namaAyat}</div>
          <div class=" p-1 text-xs text-white">{arti}</div>
        </div>
        <div class="w-1/5 flex items-center text-white  p-1 text-sm">
          {arabic}
        </div>
      </div>
    </>
  );
};

export default Ayat;
